
.bar{
    width: 100svw;
    height: 75px;
    box-shadow: 1px 1px 5px grey;
    display: flex;
    flex-direction: row;
    position: fixed;
    background-color: #eff7ff;
    z-index: 1000;
}
.sidebar{
    display: none;
}
.bar > .option{
    width: fit-content;
    height: fit-content;
    margin:auto;
    display: flex;
    flex-direction: row;
    position: relative;
}
.option > form{
    display: flex;
    flex-direction: row;
    width: 200px;
    margin: auto 20px;
    height: fit-content;
    border-radius: 1000px;
    overflow: hidden;
}
.option > div > a{
    font-family: var(--font);
    color: var(--font-color);
    text-decoration: none;
    font-size: 20px;
    font-weight: 600;
    margin: 5px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    height: 30px;
    width:calc(100% - 10px);
    transition: 0.3s;
    text-align: center;
    &:hover{
        color: white;
        border-radius: 5px;
        background-color: var(--prime);
    }
}
.option > form > button{
    background-color: white;
    margin: 0;
    outline: 0;
    border: 0;
    border-radius: 10px 0px 0px 10px;
}
.option > form > button > img{
    width: 18px;
    height: fit-content;
    margin: 5px 7px;
}
.option > form > input{
    width: 100%;
    height:30px ;
    border-radius: 0px 10px 10px 0px;
    outline: 0;
    border: 0;
    background-color: #ffffff;
    font-family: var(--font);
    font-size: 18px;
    padding: 10px 25px;
    padding-left: 5px;
}
.option > a{
    font-family: var(--font);
    color: var(--font-color);
    text-decoration: none;
    font-size: 20px;
    font-weight: 600;
    margin: auto 20px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    height: 30px;
}
.option > a > span{
    position: absolute;
    border-bottom: 3px solid var(--prime);
    width: 0px;
    height: 0px;
    bottom: 0;
    left: 0;
    transition: linear 0.4s;
    
}
.option > a:hover > span{
    width: 90px;
}
.buttons{
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: row;
    margin: auto 30px;
    justify-content: space-between;
    
}
.login,.signup{
    width: 100px;
    height: 40px;
    border-radius: 50px;
    color: white;
    background-color: var(--prime);
    outline: 0;
    border: 0;
    font-family: var(--font);
    font-size: 18px;
    margin: auto 10px;
    cursor:pointer;
    position: relative;
    margin-left: auto;
}
.cart{
    width: 30px;
    height: 40px;
    margin: auto;
    position: relative;
}
.cart > svg{
    fill: #3e54d3cf;
}
.logo{
    width: fit-content;
    height:100%;
    margin-left: 25px;
    margin-right: auto;
    cursor: pointer;
}
.banner{
    width: 100vw;
    height: 100vh;
    background-image: url(/src/Assets/Images/wallpaper.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    /* background */
    display: flex;
    flex-direction: column;
}
.banner > h3 {
    margin: auto;
    padding-top: 80px;
    font-family: var(--font);
    font-size: 60px;
    text-align: center;
    color: var(--prime);
    font-weight: 500;
}
.banner > h3 > h4{
    margin:0;
    font-size: 18px;
    color: rgb(68, 68, 68);
    font-weight: 600;
}
svg{
    height: 18px;
    fill:var(--prime);
    width: 20px;
}
@media screen and (max-width:650px) {
    
    .banner{
        background-image: url(/src/Assets/Images/wallpaper_phone.jpg);
    }
    .banner > h3{
        font-size: 50px;
        box-sizing: border-box;
        padding: 0px 20px;
    }
    .banner > h3 > h4{
        font-size: 15px;
    }
    .bar{
        height: 50px;
        flex-direction: column;
        overflow: hidden;
    }
    .logo{
        height: 60px;
    }
    .bar > .option{
       flex-direction: column;
       height: 50vh;
       margin: 0 auto;
       text-align: left;
    }
    .option > form{
        order: -1;
        width: calc(100vw - 40px);
        text-align: left;
    }
    #drop{
        height: 50vh;
    }
    .option > div > a{
        text-align: left;
        margin:auto 20px;
        width: calc(100vw - 40px);
    }
    .option > a:hover{
        &hover{
            color: white;
        border-radius: 5px;
        background-color: var(--prime);
        }
    }
    .buttons{
        display: grid;
        grid-template-columns: auto auto;
        height: 150px;
        margin: 0 auto;
        margin-top: auto;
        justify-content: center;
    }
    
    
}
